import React, { FC, ReactElement, useState, useEffect } from "react";
import {
  MdAccountCircle,
  MdCheckBox,
  MdCheckBoxOutlineBlank,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  DocumentInterface,
  FC_UpdateDocument,
} from "../../actions";
import { StoreState } from "../../reducers";
import Alert from "../Alert/Alert";
import Button from "../Buttons/Button";
import Modal from "../Modal/Modal";

type DocumentEditCommentProps = {
  auth: Auth;
  basic: Basic;
  close: () => void;
  title: string;
  comment: string | null;
  access: string | null;
  domain_name: string | null;
  access_key: string;
  comment_key: string;
  date_key: string;
  date: Date | null;
  document_id: string;
  user_id_key: string;
  updateRows: (data_fn: DocumentInterface) => void;
};

interface ErrorInterface {
  target: "comment" | "view-list" | "main-error" | "main-success" | null;
  msg: string;
}

const _DocumentEditComment: FC<DocumentEditCommentProps> = ({
  auth,
  close,
  title,
  comment,
  basic,
  access,
  domain_name,
  comment_key,
  date_key,
  date,
  access_key,
  document_id,
  user_id_key,
  updateRows,
}): ReactElement => {
  const [canView, setCanView] = useState<string>(access === null ? "" : access);
  const [newComment, setComment] = useState<string>(
    comment === null ? "" : comment
  );
  const [Error, setError] = useState<ErrorInterface>({
    target: null,
    msg: "",
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (access === null) {
      let data_access = basic.roles.map((itm) => itm.constant);
      setCanView(data_access.toString());
      console.log(data_access.toString());
    }
  }, [basic.roles, access]);

  const setCommentView = (constant: string) => {
    setError({ msg: "", target: null });
    if (canView.indexOf(constant) >= 0) {
      setCanView(canView.replace(constant, " "));
    } else {
      setCanView(canView + ` ${constant} `);
    }
  };

  const submitData = (e: any) => {
    e.preventDefault();

    if (comment === "")
      return setError({
        target: "comment",
        msg: "Le commentaire ne doit pas être vide",
      });

    if (canView.length <= 1)
      return setError({
        target: "view-list",
        msg: "Veuillez sélectionner un utilisateur qui peut voir votre commentaire",
      });

    if (auth.user === null)
      return setError({
        target: "main-error",
        msg: "Aucun accès trouvé",
      });

    let Data: {
      document_id: string;
      [el_title: string]: Date | string | number;
    } = {
      document_id: document_id,
      [comment_key]: newComment,
      [access_key]: canView,
      [user_id_key]: auth.user?.user_id,
    };

    if (date === null && date_key !== null)
      Data[date_key] = new Date().toISOString();

    setLoading(true);
    FC_UpdateDocument(Data, (status, data, msg) => {
      if (status === true && data !== null) {
        updateRows(data);
        setError({
          target: "main-success",
          msg: "Le commentaire a été mis à jour avec succès",
        });
      } else {
        setError({
          target: "main-error",
          msg: msg,
        });
      }
      setLoading(false);
      setTimeout(() => {
        setError({
          target: null,
          msg: "",
        });
      }, 3000);
    });
  };
  return (
    <Modal title={title} close={loading ? () => {} : close}>
      <form onSubmit={submitData}>
        <div className="">
          {Error.target === "main-error" && (
            <Alert theme="danger">{Error.msg}</Alert>
          )}
          {Error.target === "main-success" && (
            <Alert theme="success">{Error.msg}</Alert>
          )}
          <div className="p-2">
            <label htmlFor="comment-item" className="mb-2 text-gray-500">
              Ajouter un commentaire:
            </label>
            <textarea
              onChange={(e) => {
                setComment(e.target.value);
                setError({ msg: "", target: null });
              }}
              value={newComment}
              className={`py-3 px-2 w-full rounded border-2 ${
                Error.target === "comment" ? "border-red-400" : ""
              }`}
              disabled={loading}
              id="comment-item"
              cols={30}
              rows={5}
            ></textarea>
            {Error.target === "comment" && (
              <span className="text-danger text-sm">{Error.msg}</span>
            )}
          </div>
          <div className="border-t">
            <div className="px-2 text-center pt-3">
              <h3 className="font-bold text-lg">
                Choisissez qui peut voir votre commentaire
              </h3>
              <p className="text-sm text-gray-500 pb-1">
                Cliquez sur l'icône de l'utilisateur pour sélectionner et
                désélectionner
              </p>
              {Error.target === "view-list" && (
                <Alert theme="success">{Error.msg}</Alert>
              )}
            </div>
            <div className="p-2 grid grid-cols-2 gap-2">
              {basic.roles
                .filter((el) => el.constant !== auth.selected_access?.constant)
                .map((itm, i) => {
                  let exist = canView.indexOf(itm.constant) >= 0 ? true : false;

                  return (
                    <div
                      key={i}
                      className={` rounded shadow ${
                        exist
                          ? " bg-blue-50 text-blue-700 border border-blue-400"
                          : "bg-white"
                      } p-0.5 flex items-center cursor-pointer`}
                      onClick={setCommentView.bind(this, itm.constant)}
                    >
                      <div className="">
                        <MdAccountCircle className="text-3xl opacity-90" />
                      </div>
                      <div className="flex-1 pl-2">
                        <h4 className="font-bold">{itm.role_name}</h4>
                      </div>
                      <div className="">
                        <h6 className="opacity-60 text-sm px-2">
                          {domain_name}
                        </h6>
                      </div>
                      <div className="pr-1">
                        {exist ? (
                          <MdCheckBox className="text-2xl text-blue-600" />
                        ) : (
                          <MdCheckBoxOutlineBlank className="text-2xl text-gray-600" />
                        )}
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
          <div className="p-2 border-t text-right text-gray-500 bg-white rounded-b-lg">
            <Button loading={loading}>Mettre à jour le commentaire</Button>
          </div>
        </div>
      </form>
    </Modal>
  );
};

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

const DocumentEditComment = connect(mapStateToProps, {})(_DocumentEditComment);

export default DocumentEditComment;
