import React, { FC, ReactElement, useState } from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import {
  Auth,
  Basic,
  DocumentInterface,
  FC_UpdateDocument,
} from "../../actions";
import Button from "../../components/Buttons/Button";
import { DocumentDetailsError } from "../DocumentDetails/DocumentDetails";

type AppProps = {
  updateRows: (data: DocumentInterface) => void;
  title: string;
  data: {
    document_id: string;
    [value: string]: string;
  };
  setError: (error: DocumentDetailsError) => void;
  confirm?: string;
  danger?: boolean;
};

const _Component: FC<AppProps> = ({
  data,
  title,
  updateRows,
  setError,
  confirm,
  danger,
}): ReactElement => {
  const [loading, setLoading] = useState(false);

  const submitData = () => {
    if (confirm === undefined) {
      submitDataAPI();
    } else {
      if (window.confirm(confirm)) {
        submitDataAPI();
      }
    }
  };
  const submitDataAPI = () => {
    setLoading(true);
    FC_UpdateDocument(data, (status, data, msg) => {
      if (status === true && data !== null) {
        updateRows(data);
        setError({ target: "main-success", msg: msg });
      } else {
        setError({
          target: "main-error",
          msg: msg,
        });
      }
      setLoading(false);
      setTimeout(() => {
        setError({
          target: null,
          msg: "",
        });
      }, 3000);
    });
  };

  return (
    <div className="my-1.5">
      <Button
        loading={loading}
        onClick={submitData}
        theme={danger ? "danger-light" : "success-light"}
        className="w-full"
      >
        {title}
      </Button>
    </div>
  );
};

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { basic: basic, auth: auth };
};

const SendLetterTo = connect(mapStateToProps, {})(_Component);

export default SendLetterTo;
