import React, { FC, ReactElement, useState } from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import Modal from "../../components/Modal/Modal";
import {
  Auth,
  Basic,
  BasicDomain,
  DocumentInterface,
  FC_UpdateDocument,
} from "../../actions";
import InputSelect from "../../components/Inputs/InputSelect";
import InputSelectCustom from "../../components/Inputs/InputSelectCustom";
import InputPlaceholder from "../../components/Inputs/InputPlaceholder";
import Button from "../../components/Buttons/Button";

type AppProps = {
  close: () => void;
  auth: Auth;
  basic: Basic;
  domain_id: string | null | undefined;
  priority_id: string | null | undefined;
  document_id: string;
  updateRows: (data: DocumentInterface) => void;
};

const _Component: FC<AppProps> = ({
  close,
  domain_id,
  priority_id,
  basic,
  document_id,
  auth,
  updateRows,
}): ReactElement => {
  const [domain, setDomain] = useState(domain_id);
  const [priority, setPriority] = useState(priority_id);
  const [loading, setLoading] = useState(false);
  const [domain_open, setDomainOpen] = useState(false);
  const [error, setError] = useState<{
    target: "main-success" | "main-failed" | "domain" | "priority" | null;
    msg: string;
  }>({
    target: null,
    msg: "",
  });

  const submitData = (e: any) => {
    e.preventDefault();
    if (domain === "" || domain === null || domain === undefined)
      return setError({
        target: "domain",
        msg: "Le domaine est requis",
      });

    if (priority === "" || priority === null || priority === undefined)
      return setError({
        target: "priority",
        msg: "La catégorie est obligatoire",
      });

    if (auth.user === null) return;

    setLoading(true);
    FC_UpdateDocument(
      {
        document_id: document_id,
        domain_id: domain,
        priority_id: priority,
        cecat_view: "1",
        cecat_id: auth.user.user_id,
      },
      (status, data, msg) => {
        if (status === true && data !== null) {
          updateRows(data);
          close();
        } else {
          setError({
            target: "main-failed",
            msg: msg,
          });
        }
        setLoading(false);
        setTimeout(() => {
          setError({
            target: null,
            msg: "",
          });
        }, 3000);
      }
    );
  };

  let selected_domain = basic.domains.find((itm) => itm.domain_id === domain);

  return (
    <Modal title="Add Domain and priority" close={close}>
      <form onSubmit={submitData}>
        <div className="p-4">
          <div className="px-2 py-2">
            {domain_open === false ? (
              <div className="">
                <InputPlaceholder
                  onClick={() => setDomainOpen(true)}
                  value={selected_domain?.domain_name}
                  error={error.target === "domain" ? error.msg : ""}
                  label={"Sélectionnez le domaine"}
                  placeholder={"Sélectionnez le domaine"}
                />
              </div>
            ) : (
              <InputSelectCustom
                click={(itm: BasicDomain) => {
                  setDomain(itm.domain_id);
                  setDomainOpen(false);
                }}
                close={setDomainOpen.bind(this, false)}
                loading={loading}
                data={[{ title: "", value: "" }, ...basic.domains]}
                id="domain_id"
                title={"domain_name"}
              />
            )}
          </div>

          <div className="px-2 py-2">
            <InputSelect
              onChange={(e) => setPriority(e.target.value)}
              value={priority + ""}
              error={error.target === "priority" ? error.msg : ""}
              label={"Sélectionnez la priorité"}
              placeholder={"Sélectionnez la priorité"}
              name={""}
              data={[
                { title: "", value: "" },
                ...basic.priorities.map((itm) => ({
                  title: itm.title,
                  value: itm.id,
                })),
              ]}
              disabled={loading}
            />

            <div className="py-2 text-right">
              <Button loading={loading}>Mettre à jour le document</Button>
            </div>
          </div>
        </div>
      </form>
    </Modal>
  );
};

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { basic: basic, auth: auth };
};

const AddDomainAndPriority = connect(mapStateToProps, {})(_Component);

export default AddDomainAndPriority;
