import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  DocumentDetails_API,
  DocumentInterface,
  FC_GetDocumentDetails,
  FC_UpdateDocument,
} from "../../actions";
import PDFViewer from "../../components/PDFViewer/PDFViewer";
import { DOCUMENT_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { DATE, TIME } from "../../utils/functions";
import Button from "../Buttons/Button";
import DocumentDetailsHeader from "./DocumentDetailsHeader";
import { DocumentLoading, FileLoading } from "./DocumentLoading";
import Alert from "../../components/Alert/Alert";
import { FcDocument, FcPlus } from "react-icons/fc";
import AddDomainAndPriority from "../../containers/DocumentsList/AddDomainAndPriority";
import SendLetterTo from "../SendLetterTo/SendLetterTo";
import DocumentCommentView from "./DocumentCommentView";
import DocumentEditComment from "./DocumentEditComment";
// import { MdAccountCircle, MdComment } from "react-icons/md";
// import UserListItem from "./UserListItem";
// import { BsStickyFill } from "react-icons/bs";
import {
  MdCheckCircle,
  MdCommentBank,
  MdOutlineDone,
  MdOutlineDoneAll,
} from "react-icons/md";
import { UserRoles } from "../../utils/access";
import Modal from "../Modal/Modal";
import DocumentSubCommentView from "./DocumentSubComment";
// props for the component
interface AppProps {
  auth: Auth;
  document_id: string;
  basic: Basic;
  close: () => void;
  updateRows: (data: DocumentInterface) => void;
  selectDocument: (id: string) => void;
}

export interface DocumentDetailsError {
  target: "main-error" | "main-success" | null;
  msg: string;
}
interface AppState {
  loading: boolean;
  loading_document: boolean;
  error: DocumentDetailsError;
  document: DocumentDetails_API | null;
  open_add_domain_and_priority: boolean;
  governor_comment_open: boolean;
  expert_comment_open: boolean;
  director_comment_open: boolean;
  ministor_open: boolean;
  ministor_comment_open: boolean;
  governor_comment_edit: boolean;
  expert_comment_edit: boolean;
  director_comment_edit: boolean;
  ministor_edit: boolean;
  ministor_comment_edit: boolean;
  open_document_reference: boolean;
}

class _DocumentDetails extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loading_document: true,
      error: {
        target: null,
        msg: "",
      },
      document: null,
      open_add_domain_and_priority: false,
      governor_comment_open: false,
      expert_comment_open: false,
      director_comment_open: false,
      ministor_open: false,
      ministor_comment_open: false,
      governor_comment_edit: false,
      expert_comment_edit: false,
      director_comment_edit: false,
      ministor_edit: false,
      ministor_comment_edit: false,
      open_document_reference: false,
    };
  }

  updateRows = (data: DocumentInterface) => {
    if (this.state.document !== null) {
      this.setState({
        document: {
          document: data,
          users: this.state.document.users,
          reference_document: this.state.document.reference_document,
        },
        error: {
          target: "main-success",
          msg: "Document updated successfully",
        },
      });

      this.clearErrors();
    }
    this.props.updateRows(data);
  };

  updateDocument = (
    data: { [data: string]: any },
    show_message: boolean = false
  ): void => {
    FC_UpdateDocument(
      { document_id: this.props.document_id, ...data },
      (status, document, error) => {
        if (status === true && document !== null) {
          if (this.state.document !== null)
            this.setState({
              document: {
                users: this.state.document.users,
                document: document,
                reference_document: this.state.document.reference_document,
              },
              error: {
                target: show_message === true ? "main-success" : null,
                msg:
                  show_message === true
                    ? "Document mis à jour avec succès"
                    : "",
              },
            });
        } else {
          this.setState({
            error: {
              msg: error,
              target: "main-error",
            },
          });
        }
      }
    );
  };

  componentDidMount() {
    FC_GetDocumentDetails(this.props.document_id, (status, document, msg) => {
      if (status === true && document !== null) {
        this.setState({
          loading: false,
          document: document,
        });
        //* CECAT view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.CECAT &&
          document.document.cecat_view_date === null
        ) {
          this.updateDocument({ cecat_view_date: new Date(), cecat_view: "1" });
        }

        //* Expeditor view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.EXPEDITION &&
          document.document.expeditor_view_date === null
        ) {
          this.updateDocument({
            expeditor_view_date: new Date(),
            expeditor_view: "1",
          });
        }

        //* Expert view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.EXPERT &&
          document.document.expert_view_date === null
        ) {
          this.updateDocument({
            expert_view_date: new Date(),
            expert_view: "1",
          });
        }

        //* Governor view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.GOVENEUR &&
          document.document.governor_view_date === null
        ) {
          this.updateDocument({
            governor_view_date: new Date(),
            governor_view: "1",
          });
        }

        //* MINISTOR view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.MINISTOR &&
          document.document.minister_view_date === null
        ) {
          this.updateDocument({
            minister_view_date: new Date(),
            minister_view: "1",
          });
        }

        //* DIrector view document
        if (
          this.props.auth.selected_access !== null &&
          this.props.auth.selected_access.constant === UserRoles.DIRECTOR &&
          document.document.director_view_date === null
        ) {
          this.updateDocument({
            director_view_date: new Date(),
            director_view: "1",
          });
        }
      } else {
        this.setState({
          loading: false,
          // document: document,
          error: {
            target: "main-error",
            msg: msg,
          },
        });
        this.clearErrors();
      }
    });
  }

  clearErrors = () =>
    setTimeout(() => {
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
    }, 3000);

  render() {
    let doc = this.state.document;
    let domain_name: any = "";
    let priority_name: any = "";

    if (doc !== null) {
      if (doc.document.domain_id !== null)
        domain_name = this.props.basic.domains.find(
          (itm) => itm.domain_id === doc?.document.domain_id
        )?.domain_name;

      if (doc.document.priority_id !== null)
        priority_name = this.props.basic.priorities.find(
          (itm) => itm.id + "" === doc?.document.priority_id + ""
        )?.title;
    }

    // console.log(this.state.document);

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <div className="fixed top-0 overflow-auto right-0 left-0 bottom-0 z-50 bg-white animate__animated animate__zoomIn animate__faster">
          <DocumentDetailsHeader
            close={this.props.close}
            document_code={doc === null ? "" : doc.document.document_code}
          />
          {this.state.loading === true ? (
            <DocumentLoading />
          ) : (
            <div className=" flex bg-white lg:flex-row-reverse flex-col ">
              {/** *********************************** DOCUMENT INFO *********************************** */}
              {this.state.document !== null && (
                <div className="lg:w-2/6  lg:overflow-y-auto height-calculated">
                  {this.state.document.document.governor_signed !== null &&
                    +this.state.document.document.governor_signed === 1 && (
                      <div className="flex items-center p-1 mt-1 rounded bg-green-500 text-white mx-3">
                        <div className="pr-2">
                          <MdCheckCircle className="text-3xl" />
                        </div>
                        <div className="flex-1">
                          Ce document est approuvé par le gouverneur
                        </div>
                      </div>
                    )}

                  <div className="p-5 border-b">
                    {console.log([
                      {
                        title: "Code du document",
                        value: doc?.document.document_code,
                      },
                      {
                        title: "Nom du propriétaire du document",
                        value: doc?.document.owner_name,
                      },
                      {
                        title: "E-mail du propriétaire du document",
                        value: doc?.document.owner_email,
                      },
                      {
                        title: "Téléphone du propriétaire du document",
                        value: doc?.document.owner_phone,
                      },
                      {
                        title: "Institution",
                        value: doc?.document.owner_institute,
                      },
                      {
                        title: "Date d'enregistrement",
                        value: DATE(doc?.document.registered_date),
                      },
                    ])}
                    {[
                      {
                        title: "Code du document",
                        value: doc?.document.document_code,
                      },
                      {
                        title: "Nom du propriétaire du document",
                        value: doc?.document.owner_name,
                      },
                      {
                        title: "E-mail du propriétaire du document",
                        value: doc?.document.owner_email,
                      },
                      {
                        title: "Téléphone du propriétaire du document",
                        value: doc?.document.owner_phone,
                      },
                      {
                        title: "Institution",
                        value: doc?.document.owner_institute,
                      },
                      {
                        title: "Date d'enregistrement",
                        value: DATE(doc?.document.registered_date),
                      },
                    ].map((el, i) => {
                      if (
                        el.value !== null &&
                        el.value !== undefined &&
                        el.value?.length >= 2
                      ) {
                        return (
                          <div
                            key={i}
                            className="py-1 border-b border-gray-100 hover:bg-gray-100"
                          >
                            <span className="text-gray-500 block text-sm">
                              {el.title}:
                            </span>{" "}
                            <b>{el.value}</b>
                          </div>
                        );
                      } else {
                        return null;
                      }
                    })}
                    {this.state.document.reference_document !== null && (
                      <button
                        className="p-1 items-center border flex mt-2 w-full rounded bg-blue-100 text-blue-800 hover:bg-blue-200"
                        onClick={() =>
                          this.setState({ open_document_reference: true })
                        }
                      >
                        <div className="pr-1">
                          <FcDocument className="text-4xl" />
                        </div>
                        <div className="flex-1 text-left">
                          <p>
                            Ce document est associé à un autre document{" "}
                            <b>
                              {
                                this.state.document.reference_document
                                  .document_code
                              }
                            </b>{" "}
                          </p>
                        </div>
                      </button>
                    )}
                  </div>
                  <div className="p-5 border-b">
                    {domain_name && (
                      <div className="py-1 border-b border-gray-100 flex items-center group">
                        <div className="flex-1">
                          <span className="text-gray-500">Domain:</span>{" "}
                          <b>{domain_name}</b>
                        </div>
                        <div className="">
                          {this.props.auth.selected_access?.constant ===
                            UserRoles.CECAT && (
                            <button
                              className="text-xs py-1 px-2 group-hover:bg-blue-200 rounded bg-blue-50 text-blue-700"
                              onClick={() =>
                                this.setState({
                                  open_add_domain_and_priority: true,
                                })
                              }
                            >
                              Modifier
                            </button>
                          )}
                        </div>
                      </div>
                    )}

                    {priority_name && (
                      <div className="py-1 border-gray-100 flex items-center group">
                        <div className="flex-1">
                          <span className="text-gray-500">Priority:</span>{" "}
                          <b>{priority_name}</b>
                        </div>
                        <div className="">
                          {this.props.auth.selected_access?.constant ===
                            UserRoles.CECAT && (
                            <button
                              className="text-xs py-1 px-2 group-hover:bg-blue-200 rounded bg-blue-50 text-blue-700"
                              onClick={() =>
                                this.setState({
                                  open_add_domain_and_priority: true,
                                })
                              }
                            >
                              Modifier
                            </button>
                          )}
                        </div>
                      </div>
                    )}

                    {doc !== null &&
                      (doc.document.domain_id === null ||
                        doc.document.priority_id === null) && (
                        <div className="group my-2 p-2 bg-blue-50 hover:bg-blue-100 border-blue-100 rounded-lg border-2 text-center">
                          <FcPlus className="text-6xl mx-auto" />
                          <p className="mt-1 mb-2 font-bold">
                            Ajouter un domaine et une priorité
                          </p>
                          <Button
                            theme="accent-light"
                            className="border-2 border-blue-500 group-hover:bg-blue-600 group-hover:text-white "
                            size="sm"
                            onClick={() =>
                              this.setState({
                                open_add_domain_and_priority: true,
                              })
                            }
                          >
                            Mettre à jour le document
                          </Button>
                        </div>
                      )}
                  </div>

                  {/** *********************************** COMMENT VIEW *********************************** */}

                  <div className="border-b p-5">
                    {(this.state.document.document.expert_comment !== null ||
                      this.state.document.document.director_comment !== null ||
                      this.state.document.document.governor_comment !== null ||
                      this.state.document.document.minister_comment !==
                        null) && (
                      <>
                        <h3 className="font-bold">Commentaires</h3>

                        <div className="py-2  grid ">
                          {this.state.document.document.governor_comment !==
                            null &&
                            this.state.document.document
                              .governor_comment_access !== null &&
                            this.state.document.document.governor_comment_access.indexOf(
                              this.props.auth.selected_access?.constant + ""
                            ) >= 0 && (
                              <DocumentSubCommentView
                                OnClick={() =>
                                  this.setState({ governor_comment_open: true })
                                }
                                user={doc?.users.govenor}
                                comment={doc?.document.governor_comment}
                                date={doc?.document.governor_comment_date}
                                title={"Commentaire du gouverneur"}
                              />
                            )}

                          {this.state.document.document.director_comment !==
                            null &&
                            this.state.document.document
                              .director_comment_access !== null &&
                            this.state.document.document.director_comment_access.indexOf(
                              this.props.auth.selected_access?.constant + ""
                            ) >= 0 && (
                              <DocumentSubCommentView
                                OnClick={() =>
                                  this.setState({ director_comment_open: true })
                                }
                                user={doc?.users.director}
                                comment={doc?.document.director_comment}
                                date={doc?.document.director_comment_date}
                                title={"Commentaire de durecteur"}
                              />
                            )}

                          {this.state.document.document.minister_comment !==
                            null &&
                            this.state.document.document
                              .ministor_comment_access !== null &&
                            this.state.document.document.ministor_comment_access.indexOf(
                              this.props.auth.selected_access?.constant + ""
                            ) >= 0 && (
                              <DocumentSubCommentView
                                OnClick={() =>
                                  this.setState({ ministor_comment_edit: true })
                                }
                                user={doc?.users.minister}
                                comment={doc?.document.minister_comment}
                                date={doc?.document.ministor_comment_date}
                                title={"Commentaire du ministre"}
                              />
                            )}

                          {this.state.document.document.expert_comment !==
                            null &&
                            this.state.document.document
                              .expert_comment_access !== null &&
                            this.state.document.document.expert_comment_access.indexOf(
                              this.props.auth.selected_access?.constant + ""
                            ) >= 0 && (
                              <DocumentSubCommentView
                                OnClick={() =>
                                  this.setState({ expert_comment_open: true })
                                }
                                user={doc?.users.expert}
                                comment={doc?.document.expert_comment}
                                date={doc?.document.expert_comment_date}
                                title={"Commentaire d'expert"}
                              />
                            )}
                        </div>
                      </>
                    )}

                    <div className="p">
                      {this.props.auth.selected_access?.constant ===
                        UserRoles.GOVENEUR && (
                        <Button
                          theme="accent-light"
                          full
                          onClick={() =>
                            this.setState({ governor_comment_edit: true })
                          }
                        >
                          <MdCommentBank className="inline-block pr-2 text-2xl" />
                          {this.state.document.document.governor_comment ===
                          null
                            ? "Ajoutez votre commentaire (Gouverneur)"
                            : "Modifier votre commentaire (Gouverneur)"}
                        </Button>
                      )}

                      {this.props.auth.selected_access?.constant ===
                        UserRoles.DIRECTOR && (
                        <Button
                          theme="accent-light"
                          full
                          onClick={() =>
                            this.setState({ director_comment_edit: true })
                          }
                        >
                          <MdCommentBank className="inline-block pr-2 text-2xl" />
                          {this.state.document.document.governor_comment ===
                          null
                            ? "Ajoutez votre commentaire (Directeur)"
                            : "Modifier votre commentaire (Directeur)"}
                        </Button>
                      )}

                      {this.props.auth.selected_access?.constant ===
                        UserRoles.MINISTOR && (
                        <Button
                          theme="accent-light"
                          full
                          onClick={() =>
                            this.setState({ ministor_comment_edit: true })
                          }
                        >
                          <MdCommentBank className="inline-block pr-2 text-2xl" />
                          {this.state.document.document.governor_comment ===
                          null
                            ? "Ajoutez votre commentaire (Ministre)"
                            : "Modifier votre commentaire (Ministre)"}
                        </Button>
                      )}

                      {this.props.auth.selected_access?.constant ===
                        UserRoles.EXPERT && (
                        <Button
                          theme="accent-light"
                          full
                          onClick={() =>
                            this.setState({ expert_comment_edit: true })
                          }
                        >
                          <MdCommentBank className="inline-block pr-2 text-2xl" />
                          {this.state.document.document.governor_comment ===
                          null
                            ? "Ajoutez votre commentaire (Expert)"
                            : "Modifier votre commentaire (Expert)"}
                        </Button>
                      )}
                    </div>
                  </div>

                  {/** *********************************** SEND LETTER *********************************** */}
                  <div className="p-5 border-b">
                    {/* <h3 className="font-bold">Send letters</h3> */}
                    <div className="py-2">
                      {/* CECAT SEND TO DIRECTOR */}
                      {this.props.auth.selected_access?.constant ===
                        UserRoles.CECAT && (
                        <>
                          {this.state.document.document.director_access ===
                          0 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                director_access: "1",
                              }}
                              title="Envoyer le document au directeur"
                            />
                          ) : this.state.document?.document?.director_view !==
                            null ? (
                            <div className="p-1 bg-gray-100 flex items-center">
                              <div className="pr-2">
                                <MdOutlineDoneAll className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le directeur a reçu le document
                              </div>
                            </div>
                          ) : (
                            <div className="p-1 bg-gray-100 flex items-center">
                              <div className="pr-2">
                                <MdOutlineDone className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le document a été envoyé au directeur
                              </div>
                            </div>
                          )}
                        </>
                      )}

                      {/* DIRECTOR SEND TO GOVENOR */}
                      {this.props.auth.selected_access?.constant ===
                        UserRoles.DIRECTOR && (
                        <>
                          {this.state.document.document.governor_access ===
                          0 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                governor_access: "1",
                              }}
                              title="Envoyer le document au gouverneur"
                            />
                          ) : this.state.document?.document?.governor_view !==
                            null ? (
                            <div className="p-1 bg-gray-100 flex items-center">
                              <div className="pr-2">
                                <MdOutlineDoneAll className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le gouverneur a reçu le document
                              </div>
                            </div>
                          ) : (
                            <div className="p-1 bg-gray-100 flex items-center">
                              <div className="pr-2">
                                <MdOutlineDone className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le document a été envoyé au gouverneur
                              </div>
                            </div>
                          )}
                        </>
                      )}

                      {/* GOVENOR SEND LETTER TO THE REST */}
                      {this.props.auth.selected_access?.constant ===
                        UserRoles.GOVENEUR && (
                        <>
                          {/* GOVENOR SEND LETTER TO MINISTOR */}
                          {this.state.document.document.governor_access ===
                          0 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                ministor_access: "1",
                              }}
                              title={`Envoyer le document au ministre (${domain_name})`}
                            />
                          ) : this.state.document?.document?.minister_view !==
                            null ? (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDoneAll className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le ministre a reçu le document
                              </div>
                            </div>
                          ) : (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDone className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le document a été envoyé au ministre
                              </div>
                            </div>
                          )}

                          {/* GOVENOR SEND LETTER TO EXPERT */}
                          {this.state.document.document.expert_access === 0 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                expert_access: "1",
                              }}
                              title={`Envoyer le document aux experts (${domain_name})`}
                            />
                          ) : this.state.document?.document?.expeditor_view !==
                            null ? (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDoneAll className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Les experts ont reçu le document
                              </div>
                            </div>
                          ) : (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDone className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Le document a été envoyé aux experts
                              </div>
                            </div>
                          )}

                          {/* GOVENOR SEND LETTER TO EXPEDITOR */}
                          {this.state.document.document.expeditor_access ===
                          0 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                expeditor_access: "1",
                              }}
                              title={`Envoyer le document aux expéditeurs `}
                            />
                          ) : this.state.document?.document?.expeditor_view !==
                            null ? (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDoneAll className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                Les expéditeurs ont reçu le document
                              </div>
                            </div>
                          ) : (
                            <div className="p-1 bg-gray-100 flex items-center border-b">
                              <div className="pr-2">
                                <MdOutlineDone className="text-2xl text-green-500" />
                              </div>
                              <div className="flex-1">
                                le document a été envoyé aux expéditeurs
                              </div>
                            </div>
                          )}

                          {this.props.auth.selected_access?.constant ===
                            UserRoles.GOVENEUR &&
                          this.state.document.document.governor_signed !==
                            null &&
                          +this.state.document.document.governor_signed !==
                            1 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                governor_signed: "1",
                              }}
                              title="Approuver le document"
                              confirm="Voulez-vous vraiment approuver ce document ?"
                            />
                          ) : (
                            <div className="p-2 bg-gray-100 text-green text-center border-b">
                              La lettre est approuvée par le gouverneur
                            </div>
                          )}

                          {this.state.document.document.to_print !== null &&
                          +this.state.document.document.to_print !== 1 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                to_print: "1",
                              }}
                              title="Envoyez cette lettre au service d'impression"
                            />
                          ) : (
                            <div className="p-2 rounded border-b bg-gray-100 text-center">
                              la lettre a été envoyée au service d'impression
                            </div>
                          )}

                          {this.state.document.document.closed !== null &&
                          +this.state.document.document.closed !== 1 ? (
                            <SendLetterTo
                              setError={(error) =>
                                this.setState({ error: error })
                              }
                              updateRows={this.updateRows}
                              data={{
                                document_id:
                                  this.state.document.document.document_id,
                                closed: "1",
                              }}
                              title="Fermer le document"
                              danger
                            />
                          ) : (
                            <div className="p-2 text-center text-red-800 bg-red-100 rounded border-b">
                              The letter has been closed
                            </div>
                          )}
                        </>
                      )}
                    </div>
                  </div>

                  {/** *********************************** CONTRIBUTORS *********************************** */}

                  <div className="border-b p-5">
                    <h3 className="font-bold">Contributrice/Contributeurs</h3>
                    <div className="y-2">
                      <table className="w-full border text-sm">
                        <thead className="text-left border">
                          <tr>
                            <th className="p-1 border">Rôle</th>
                            <th className="p-1 border">Des noms</th>
                            <th className="p-1 border">Dates et heure</th>
                          </tr>
                        </thead>
                        <tbody>
                          {[
                            {
                              user: this.state.document.users.cecat,
                              role: "CECAT",
                              date: this.state.document.document
                                .cecat_view_date,
                            },
                            {
                              user: this.state.document.users.director,
                              role: "Director",
                              date: this.state.document.document
                                .director_view_date,
                            },
                            {
                              user: this.state.document.users.expeditor,
                              role: "Expeditor",
                              date: this.state.document.document
                                .expeditor_view_date,
                            },
                            {
                              user: this.state.document.users.expert,
                              role: "Expert",
                              date: this.state.document.document
                                .expert_view_date,
                            },
                            {
                              user: this.state.document.users.govenor,
                              role: "Govenor",
                              date: this.state.document.document
                                .governor_view_date,
                            },
                            {
                              user: this.state.document.users.minister,
                              role: "Ministor",
                              date: this.state.document.document
                                .minister_view_date,
                            },
                          ].map((itm, i) => (
                            <tr key={i} className="hover:bg-blue-100">
                              <td className="p-1 border">{itm.role}</td>
                              <td className="p-1 border">
                                {itm.user &&
                                  `${itm.user.first_name} ${itm.user.middle_name} ${itm.user.last_name}`}
                              </td>

                              <td className="p-1 border text-xs">
                                {DATE(itm.date)} - {TIME(itm.date)}
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>

                  {/* Contents end here */}
                </div>
              )}
              <div className="flex-1 bg-gray-800">
                {this.state.loading_document === true && <FileLoading />}
                {doc !== null && (
                  <PDFViewer
                    file_url={`${DOCUMENT_URL}/${doc.document.document_url}`}
                    class_name={` w-full height-calculated ${
                      this.state.loading_document === true
                        ? "opacity-0"
                        : "opacity-100"
                    }`}
                    frame_title={"Détails du document"}
                    setLoadingFile={() =>
                      this.setState({ loading_document: false })
                    }
                  />
                )}
              </div>
            </div>
          )}
        </div>

        {this.state.open_add_domain_and_priority === true &&
          this.state.document !== null && (
            <AddDomainAndPriority
              close={() =>
                this.setState({ open_add_domain_and_priority: false })
              }
              domain_id={this.state.document?.document.domain_id}
              priority_id={this.state.document?.document.priority_id}
              updateRows={this.updateRows}
              document_id={this.state.document.document.document_id}
            />
          )}

        {/* govenor comment */}
        {this.state.governor_comment_open === true &&
          doc !== null &&
          doc.document.governor_comment && (
            <DocumentCommentView
              close={() => this.setState({ governor_comment_open: false })}
              comment={doc.document.governor_comment}
              date={doc.document.governor_comment_date}
              title={"Commentaire du gouverneur"}
              user={doc.users.govenor}
            />
          )}

        {/* expert comment */}
        {this.state.expert_comment_open === true &&
          doc !== null &&
          doc.document.governor_comment && (
            <DocumentCommentView
              close={() => this.setState({ expert_comment_open: false })}
              comment={doc.document.expert_comment}
              date={doc.document.expert_comment_date}
              title={"Commentaire de l'expert"}
              user={doc.users.expert}
            />
          )}

        {/* director comment */}
        {this.state.director_comment_open === true &&
          doc !== null &&
          doc.document.director_comment && (
            <DocumentCommentView
              close={() => this.setState({ director_comment_open: false })}
              comment={doc.document.director_comment}
              date={doc.document.director_comment_date}
              title={"Commentaire du réalisateur"}
              user={doc.users.director}
            />
          )}

        {/* ministor comment */}
        {this.state.ministor_comment_open === true &&
          doc !== null &&
          doc.document.minister_comment && (
            <DocumentCommentView
              close={() => this.setState({ ministor_comment_open: false })}
              comment={doc.document.minister_comment}
              date={doc.document.ministor_comment_date}
              title={"Commentaire du ministre"}
              user={doc.users.minister}
            />
          )}

        {/* govenor comment */}
        {doc !== null &&
          this.state.governor_comment_edit === true &&
          doc !== null && (
            <DocumentEditComment
              close={() => this.setState({ governor_comment_edit: false })}
              comment={doc.document.governor_comment}
              title={"Commentaire du gouverneur"}
              access={doc.document.governor_comment_access}
              domain_name={domain_name}
              comment_key={"governor_comment"}
              access_key={"governor_comment_access"}
              date_key={"governor_comment_date"}
              date={doc.document.governor_comment_date}
              updateRows={this.updateRows}
              document_id={doc.document.document_id}
              user_id_key="governor_id"
            />
          )}

        {/* govenor comment */}
        {doc !== null &&
          this.state.director_comment_edit === true &&
          doc !== null && (
            <DocumentEditComment
              close={() => this.setState({ director_comment_edit: false })}
              comment={doc.document.director_comment}
              title={"Commentaire du directeur"}
              access={doc.document.director_comment_access}
              domain_name={domain_name}
              comment_key={"director_comment"}
              access_key={"director_comment_access"}
              date_key={"director_comment_date"}
              date={doc.document.director_comment_date}
              updateRows={this.updateRows}
              document_id={doc.document.document_id}
              user_id_key="director_id"
            />
          )}

        {/* ministor_comment_edit */}
        {doc !== null &&
          this.state.ministor_comment_edit === true &&
          doc !== null && (
            <DocumentEditComment
              close={() => this.setState({ ministor_comment_edit: false })}
              comment={doc.document.minister_comment}
              title={"Ministor du directeur"}
              access={doc.document.ministor_comment_access}
              domain_name={domain_name}
              comment_key={"minister_comment"}
              access_key={"ministor_comment_access"}
              date_key={"ministor_comment_date"}
              date={doc.document.ministor_comment_date}
              updateRows={this.updateRows}
              document_id={doc.document.document_id}
              user_id_key="minister_id"
            />
          )}

        {/* expert_comment_edit */}
        {doc !== null &&
          this.state.expert_comment_edit === true &&
          doc !== null && (
            <DocumentEditComment
              close={() => this.setState({ expert_comment_edit: false })}
              comment={doc.document.expert_comment}
              title={"Expert du directeur"}
              access={doc.document.expert_comment_access}
              domain_name={domain_name}
              comment_key={"expert_comment"}
              access_key={"expert_comment_access"}
              date_key={"expert_comment_date"}
              date={doc.document.expert_comment_date}
              updateRows={this.updateRows}
              document_id={doc.document.document_id}
              user_id_key="expert_id"
            />
          )}

        {this.state.open_document_reference === true &&
          this.state.document !== null &&
          this.state.document.reference_document !== null && (
            <Modal
              close={() => this.setState({ open_document_reference: false })}
              title="Document de référence"
            >
              <div className="p-2 grid grid-cols-2 gap-2">
                {[
                  {
                    title: "Des noms: ",
                    value: this.state.document.reference_document.owner_name,
                  },
                  {
                    title: "Email: ",
                    value: this.state.document.reference_document.owner_email,
                  },
                  {
                    title: "Référence: ",
                    value:
                      this.state.document.reference_document
                        .document_reference_code,
                  },
                  {
                    title: "Institut: ",
                    value:
                      this.state.document.reference_document.owner_institute,
                  },
                  {
                    title: "Date d'enregistrement: ",
                    value: DATE(
                      this.state.document.reference_document.registered_date
                    ),
                  },
                ].map((itm, i) => {
                  console.log({ val: itm.value });
                  if (itm.value === null || itm.value.length <= 2) return null;

                  return (
                    <div
                      key={i}
                      className="p-1 border rounded flex items-center"
                    >
                      <label className="text-sm text-gray-600">
                        {itm.title}
                      </label>
                      <h6 className="font-bold pl-2">{itm.value}</h6>
                    </div>
                  );
                })}
              </div>
              <PDFViewer
                file_url={`${DOCUMENT_URL}/${this.state.document.reference_document.document_url}`}
                class_name={"w-full h-96"}
                frame_title={"Détails du document"}
                setLoadingFile={() => {
                  // this.setState({ loading_document: false })
                }}
              />
              <div className="text-right p-2">
                <Button
                  size="sm"
                  onClick={() => {
                    if (
                      this.state.document !== null &&
                      this.state.document.reference_document !== null
                    ) {
                      this.props.selectDocument(
                        this.state.document.reference_document.document_id + ""
                      );
                    }
                  }}
                >
                  Ouvrir le document
                </Button>
              </div>
            </Modal>
          )}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const DocumentDetails = connect(mapStateToProps, {})(_DocumentDetails);
