import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  DocumentInterface,
  DocumentsListInterface,
  FC_GetDocuments,
} from "../../actions";
import { DocumentDetails } from "../../components/DocumentDetails/DocumentDetails";
import { StoreState } from "../../reducers";
import Alert from "../../components/Alert/Alert";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import TextInput from "../../components/Inputs/TextInput";
import InputSelect from "../../components/Inputs/InputSelect";
import { DATE, search, shortString, TIME } from "../../utils/functions";
import { FcDocument } from "react-icons/fc";
import {
  MdAccessTime,
  MdOutlineCalendarToday,
  MdOutlineFolder,
  MdRefresh,
} from "react-icons/md";
import Button from "../../components/Buttons/Button";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  selected_document_id: string;
  documents: DocumentsListInterface[];
  selected_user_to_role: string;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  selected_category: string;
  search: string;
  selected_domain: string;
}

class _DocumentsList extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      selected_document_id: "",
      selected_user_to_role: "",
      documents: [],
      error: {
        target: null,
        msg: "",
      },
      selected_category: "",
      search: "",
      selected_domain: "",
    };
  }

  clearErrors = () =>
    setTimeout(() => {
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
    }, 3000);

  componentDidMount() {
    this.getDocuments();
  }

  getDocuments() {
    if (this.props.auth.selected_access === null)
      return this.setState({
        loading: false,
        error: {
          target: "main-error",
          msg: "Impossible de trouver le rôle, veuillez réessayer plus tard",
        },
        selected_document_id: "",
      });

    this.setState({
      loading: true,
      error: {
        target: null,
        msg: "",
      },
    });
    let access = this.props.auth.selected_access;
    FC_GetDocuments(access.constant, access.domain_id, (status, data, msg) => {
      if (status === true) {
        this.setState({
          loading: false,
          documents: data,
        });
      } else {
        this.setState({
          loading: false,
          documents: [],
          error: {
            target: "main-error",
            msg: "Échec du chargement des documents, veuillez réessayer plus tard",
          },
        });

        this.clearErrors();
      }
    });
  }

  componentDidUpdate(prevProps: AppProps) {
    if (
      prevProps.auth.selected_access?.user_to_access_id !==
      this.props.auth.selected_access?.user_to_access_id
    ) {
      this.getDocuments();
    }
  }

  closeModal = () =>
    this.setState({
      selected_document_id: "",
    });

  updateRows = (data: DocumentInterface) => {
    this.setState({
      documents: this.state.documents.map((itm) =>
        itm.document_id === data.document_id
          ? ({
              closed: data.closed,
              document_code: data.document_code,
              document_id: data.document_id,
              document_reference_code: data.document_reference_code,
              document_url: data.document_url,
              domain_id: data.domain_id,
              owner_email: data.owner_email,
              owner_institute: data.owner_institute,
              owner_name: data.owner_name,
              owner_phone: data.owner_phone,
              priority_id: data.priority_id,
              registered_by_id: data.registered_by_id,
              registered_date: data.registered_date,
              status: data.status,
            } as DocumentsListInterface)
          : itm
      ),
    });
  };

  selectDocument = (doc_id: string) =>
    this.setState({
      selected_document_id: doc_id,
    });

  selectDocumentTimer = (id: string) => {
    console.log({ id });

    this.setState({
      selected_document_id: "",
    });
    setTimeout(() => {
      this.setState({
        selected_document_id: id,
      });
    }, 100);
  };

  render() {
    if (this.state.selected_document_id !== "")
      return (
        <DocumentDetails
          document_id={this.state.selected_document_id}
          close={this.closeModal}
          updateRows={this.updateRows}
          selectDocument={this.selectDocumentTimer}
        />
      );

    let Results = this.state.documents;
    console.log({ Results });

    if (this.state.selected_category !== "")
      Results = Results.filter(
        (el) => el.priority_id === this.state.selected_category
      );
    if (this.state.selected_domain !== "")
      Results = Results.filter(
        (el) => el.domain_id === this.state.selected_domain
      );

    Results = search(Results, this.state.search, {
      document_code: true,
      document_reference_code: true,
      owner_email: true,
      owner_name: true,
      owner_phone: true,
    });
    return (
      <>
        {" "}
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}
        <div className="container px-1 -mt-5 pt-3 mx-auto">
          <div className="mt-1 border overflow-hidden border-gray-300 rounded  mx-auto bg-white">
            <div className="p-2 flex items-center">
              <div className="flex-1">
                <Heading>
                  <span className="font-medium">Documents</span>
                </Heading>
              </div>
              <div className="text-right">
                <span className="text-sm text-gray-700">Documents totaux:</span>
                <h2 className="font-bold text-xl -mt-2">
                  {this.state.documents.length}
                </h2>
              </div>
            </div>

            <div className="p-1 px-2 bg-gray-100">
              <div className="flex items-center">
                <div className="flex-1">
                  <TextInput
                    onChange={(e) => this.setState({ search: e.target.value })}
                    value={this.state.search}
                    error={""}
                    label={"Recherche"}
                    size="sm"
                    placeholder={"Code du document, noms des propriétaires,..."}
                    name={"Recherche"}
                    type="search"
                  />
                </div>

                <div className="mx-2 w-36">
                  <InputSelect
                    onChange={(e) =>
                      this.setState({ selected_category: e.target.value })
                    }
                    value={this.state.selected_category}
                    error={""}
                    label={"Priorité"}
                    placeholder={""}
                    name={""}
                    data={[
                      { title: "Toute", value: "" },
                      ...this.props.basic.priorities.map((el) => ({
                        title: el.title,
                        value: el.id,
                      })),
                    ]}
                    size="sm"
                  />
                </div>

                <div className="w-48">
                  {this.props.auth.selected_access !== null &&
                  this.props.auth.selected_access?.domain_id === "1" ? (
                    <div className="mx-2">
                      <InputSelect
                        onChange={(e) =>
                          this.setState({ selected_domain: e.target.value })
                        }
                        value={this.state.selected_domain}
                        error={""}
                        label={"Domaine"}
                        placeholder={""}
                        name={""}
                        data={[
                          { title: "Toute", value: "" },
                          ...this.props.basic.domains.map((el) => ({
                            title: el.domain_name,
                            value: el.domain_id,
                          })),
                        ]}
                        size="sm"
                      />
                    </div>
                  ) : (
                    <div className="mx-2">
                      <TextInput
                        onChange={(e) => {}}
                        value={
                          this.props.auth.selected_access?.domain_name + ""
                        }
                        error={""}
                        label={"Domaine"}
                        size="sm"
                        placeholder={"Domaine"}
                        name={"Domaine"}
                        disabled
                      />
                    </div>
                  )}
                </div>
                <div className="px-2 pt-3">
                  <Button
                    onClick={() => this.getDocuments()}
                    theme="success-light"
                  >
                    <MdRefresh className="text-3xl" />
                  </Button>
                </div>
              </div>
            </div>
          </div>

          {/* <div className="mt-2 hidden">
            <table className="w-full text-left">
              <thead className="bg-blue-200 rounded text-blue-800">
                <tr>
                  <th className="p-2">#</th>
                  <th className="p-2">Code</th>
                  <th className="p-2">Owner names</th>
                  <th className="p-2">Institution</th>
                  <th className="p-2">Domain</th>
                  <th className="p-2">Category</th>
                  <th className="p-2">Date et heure</th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading === true ? (
                  <tr>
                    <td colSpan={6} className="py-8 text-center">
                      <Loading textColor="text-blue-600" />
                    </td>
                  </tr>
                ) : Results.length === 0 ? (
                  <tr>
                    <td colSpan={6} className="py-8 text-center">
                      <Heading className="text-gray-400">
                        Aucun document trouvé trouvé
                      </Heading>
                    </td>
                  </tr>
                ) : (
                  Results.map((doc, i) => {
                    let foundDomain =
                      doc.domain_id === null
                        ? undefined
                        : this.props.basic.domains.find(
                            (itm) => itm.domain_id === doc.domain_id
                          );
                    let foundPriority =
                      doc.priority_id === null
                        ? undefined
                        : this.props.basic.priorities.find(
                            (itm) => itm.id + "" === doc.priority_id + ""
                          );
                    return (
                      <tr
                        key={doc.document_id}
                        className="hover:bg-blue-100 hover:text-blue-900 cursor-pointer"
                        onClick={this.selectDocument.bind(
                          this,
                          doc.document_id
                        )}
                      >
                        <td className="p-2 hover:font-bold border-b ">
                          {i + 1}
                        </td>
                        <td className="p-2 hover:font-bold border-b ">
                          {doc.document_code}
                        </td>
                        <td className="p-2 hover:font-bold border-b ">
                          {doc.owner_name}
                        </td>
                        <td className="p-2 hover:font-bold border-b ">
                          {doc.owner_institute}
                        </td>
                        <td className="p-2 border-b ">
                          {foundDomain === undefined
                            ? "--"
                            : foundDomain.domain_name}
                        </td>
                        <td className="p-2 border-b ">
                          {foundPriority === undefined
                            ? "--"
                            : foundPriority.title}
                        </td>
                        <td className="p-2 border-b">
                          {DATE(doc.registered_date)} -{" "}
                          {TIME(doc.registered_date)}
                        </td>
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div> */}
        </div>
        <div className="">
          {this.state.loading === true ? (
            <div className="py-20 text-center">
              <Loading textColor="text-blue-600" />
            </div>
          ) : Results.length === 0 ? (
            <div className="  rounded text-center py-20">
              <MdOutlineFolder className="text-8xl animate__shakeX animate__animated text-gray-400 mx-auto" />
              <Heading className="text-gray-400">
                Aucun document trouvé trouvé
              </Heading>
            </div>
          ) : (
            <div className="p-2">
              {Results.map((doc, i) => {
                let foundDomain =
                  doc.domain_id === null
                    ? undefined
                    : this.props.basic.domains.find(
                        (itm) => itm.domain_id === doc.domain_id
                      );
                let foundPriority =
                  doc.priority_id === null
                    ? undefined
                    : this.props.basic.priorities.find(
                        (itm) => itm.id + "" === doc.priority_id + ""
                      );
                return (
                  <div
                    key={doc.document_id}
                    className="hover:bg-blue-100 hover:border-blue-600 divide-y border md:divide-y-0 divide-x-0   lg:divide-x  md:items-center flex flex-col md:flex-row hover:text-blue-900 cursor-pointer container mx-auto bg-white rounded px-2 py-0.5 mb-2"
                    onClick={this.selectDocument.bind(this, doc.document_id)}
                  >
                    <div className="pr-2">
                      <FcDocument className="text-5xl" />
                    </div>
                    <div className="p-2 flex md:flex-col">
                      <label className="text-gray-400 text-sm pr-1">
                        Code:
                      </label>
                      <h4 className="-mt-1 font-bold md:text-lg">
                        {doc.document_code}
                      </h4>
                    </div>

                    <div className="p-2 flex-1 ">
                      <label className="text-gray-400 text-sm">
                        Le nom du propriétaire:
                      </label>
                      <h4 className="-mt-1 font-bold text-lg">
                        {doc.owner_name}
                      </h4>
                    </div>

                    <div className="p-2 w-40 group flex md:flex-col">
                      <label className="text-gray-400 pr-1 text-sm">
                        Domaine:
                      </label>
                      <h4 className="-mt-1 font-bold md:text-lg">
                        {foundDomain === undefined
                          ? "--"
                          : shortString(foundDomain.domain_name, 17)}
                      </h4>
                      {foundDomain !== undefined &&
                        foundDomain.domain_name.length > 17 && (
                          <h4 className="p-1 border absolute bg-white shadow group-hover:block group-hover:opacity-100 rounded -mt-5 opacity-0 ">
                            {foundDomain?.domain_name}
                          </h4>
                        )}
                    </div>

                    <div className="p-2 md:px-4 md:w-36 flex md:flex-col">
                      <label className="text-gray-400 pr-1 block text-sm">
                        Priorité:
                      </label>

                      {foundPriority === undefined ? (
                        "--"
                      ) : (
                        <span>
                          <span className="px-2 text-center w-auto font-bold text-sm p-0.5 rounded-full bg-blue-200 text-blue-700">
                            {foundPriority.title}
                          </span>
                        </span>
                      )}
                    </div>

                    <div className="p-2 md:w-32 flex flex-col items-start">
                      <h4 className="">
                        <MdOutlineCalendarToday className="inline-block mr-1 text-blue-600" />
                        {DATE(doc.registered_date)}
                      </h4>
                      <h4 className=" pt-1 ">
                        <MdAccessTime className="inline-block mr-1 text-blue-600" />
                        {TIME(doc.registered_date)}
                      </h4>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const DocumentsList = connect(mapStateToProps, {})(_DocumentsList);
